<template>
  <el-card class="containerPerson">
      <el-form :model="form" label-width="80px" size="small">
        <el-form-item label="用户名">
          <el-input v-model="form.nickname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="学号">
          <el-input v-model="form.stuNum" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="form.pwd" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="电话">
          <el-input v-model="form.phone" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="年级">
          <el-input v-model="form.grade" autocomplete="off"></el-input>
        </el-form-item>
         <el-form-item label="头像">
          <el-input v-model="form.avatar" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="save">确 定</el-button>
        </el-form-item>
      </el-form>
  </el-card>
</template>

<script>
export default {
    name:"Person",
    data(){
        return {
            form:{
                nickname:this.nickname ,
                stuNum:this.stuNum ,
                pwd:this.pwd,
                grade:this.grade,
                phone:this.phone,
                avatar:this.avatar,
            },
             user:localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {}
             
        }
    },
    created(){
        this.request.get("/user/personmsg/").then(res => {
                if (res) {
                    // console.log(res);
                    this.form.nickname = res.nickname;
                    this.form.stuNum = res.stuNum;
                    this.form.pwd = res.pwd;
                    this.form.phone = res.phone;
                    this.form.avatar = res.avatar;
                    this.form.grade = res.grade;
                } else {
                this.$message.error("保存失败")
                }
            })

    },
    methods:{
        save() {
            this.request.post("/user/save", this.form).then(res => {
                if (res == true) {
                    this.$message.success("保存成功")
                } else {
                    this.$message.error("保存失败")
                }
            })
        },
    }

}
</script>

<style>
.containerPerson{
    width: 500px;
    padding: 20px;
    border: 1px solid #ccc;

}
</style>